import { Component, 
  OnInit, 
  ViewChild, 
  ComponentFactoryResolver,
  ViewContainerRef } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { FormControl, FormBuilder, FormGroup, Validators} from '@angular/forms';
import { Address } from '../address';
import { FormGroupDirective, NgForm} from '@angular/forms';
import { ExpComponent } from './dynamicfield.component'


@Component({
  selector: 'app-add-flat-owner',
  templateUrl: './add-flat-owner.component.html',
  styleUrls: ['./add-flat-owner.component.scss'],
  animations: [routerTransition()]

})
export class AddFlatOwnerComponent implements OnInit {
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;

  align = 'start';
  _ref:any;

  @ViewChild('parent', { read: ViewContainerRef }) container: ViewContainerRef;


  
address = new Address();


  constructor(private _formBuilder: FormBuilder, private _cfr: ComponentFactoryResolver ) { }

  ngOnInit() {

    this.addComponent();

  this.firstFormGroup = this._formBuilder.group({
    blockCtrl: ['', Validators.required],
    nameCtrl: ['', Validators.required],
    emailCtrl: [null, [Validators.required, Validators.email]],
    contactCtrl: ['', Validators.required]
  });
  this.secondFormGroup = this._formBuilder.group({
   
  });

  
}

   
addComponent(){
    
  var comp = this._cfr.resolveComponentFactory(ExpComponent);
  var expComponent = this.container.createComponent(comp);
  expComponent.instance._ref = expComponent;
}

emailFormControl = new FormControl('', [
  Validators.required,
  Validators.email,
]);

removeObject(){
  this._ref.destroy();
}


        //Contact Validation
        NumPress(event: any) {
          const pattern = /[0-9\+\-\ ]/;
      
          let inputChar = String.fromCharCode(event.charCode);
          if (event.keyCode != 8 && !pattern.test(inputChar)) {
            event.preventDefault();
      
          }
        }
        //Only String (Name)
        StringPress(event: any) {
          const pattern = /[a-z\+\A-Z\+\-\ ]/;
      
          let inputChar = String.fromCharCode(event.charCode);
          if (event.keyCode != 8 && !pattern.test(inputChar)) {
            event.preventDefault();
          }
        }
}



